@import 'reset';
@import 'theme.default';
@import 'core';
@import 'animate';
@import 'autoheight';
@import 'lazyload';
@import 'video';
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700");

.main-container {
    font-family: 'Roboto';
    font-size: 14px;
    background-color: #333333;
}

.owl-theme {
    // Styling Next and Prev buttons
    .owl-nav {
        .owl-next,
        .owl-prev {
            -webkit-tap-highlight-color: transparent;
            position: absolute;
            top: 35%;
            width: 6em;
            height: 6em;
            background-image: url("../../img/arrow.png");
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            cursor: pointer;
        }

        .owl-prev {
            -moz-transform: scaleX(-1);
            -webkit-transform: scaleX(-1);
            -ms-transform: scaleX(-1);
            transform: scaleX(-1);
            left: 0;
        }

        .owl-next {
            right: 0;
        }
    }
}
// _____SLIDES_____

.slides-wrapper {
    position: relative;
    width: calc(100% - 25em);
    opacity: 1;
    transition: width 0.75s ease, opacity 0.5s ease;
    background-color: #000;
}
// spinner
@keyframes spinner {
    0% {
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -moz-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.slides-wrapper:before {
    animation: spinner 1s linear infinite;
    transition: opacity 0.5s ease-in-out;
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 3em;
    height: 3em;
    background-image: url("../../img/spinner.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: -1.5em 0 0 -1.5em;
    opacity: 1;
}
// end of spinner

.item {
    width: calc(100vw - 350px);
    transition: width 0.75s ease;
}

body .fullscreen {
    width: 100vw;
    overflow-x: hidden;
    transition: width 0.5s ease;

    .sidebar {
        visibility: hidden;
    }
}

body .item-fullscreen {
    width: 100vw;
    transition: width 0.5s ease;
}

.slide {
    height: 100vh;
    background-size: cover;
    background-position: center center;
}

.item h5 {
    position: absolute;
    bottom: 0;
    left: 2em;
    font-size: 1.5em;
    line-height: 1.25;
    margin: 0 0 1em;
    color: white;
}

#thumbnails .item > * {
    pointer-events: auto;
}
/*begin hamburger*/
#touch-menu {
    position: absolute;
    right: $sidebar-width;
    top: 30px;
    right: 30px;
}

#nav-icon1 {
    width: 30px;
    height: 30px;
    position: relative;
    z-index: 11111;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    cursor: pointer;
}

#nav-icon1 span {
    display: block;
    position: absolute;
    height: 1px;
    width: 100%;
    background: #fff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.25s ease-in-out;
    -moz-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
    top: 8px;
    transform: rotate(135deg);
}

#nav-icon1 span:nth-child(2) {
    opacity: 0;
    // left: -60px;
}

#nav-icon1 span:nth-child(3) {
    top: 8px;
    transform: rotate(-135deg);
}

#nav-icon1.open span:nth-child(1) {
    top: 0;
    transform: rotate(0deg);
}

#nav-icon1.open span:nth-child(2) {
    top: 10px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
}

#nav-icon1.open span:nth-child(3) {
    top: 20px;
    transform: rotate(0deg);
}
/*end hamburger*/
/*_____BEGIN OF SIDEBAR_____*/
body .toggle-sidebar {
    visibility: hidden;
    opacity: 0;
    right: -$sidebar-width;
}

.sidebar {
    position: fixed;
    top: 0;
    right: 0;
    opacity: 0;
    width: $sidebar-width;
    height: 100%;
    padding: 0.75em;
    box-sizing: border-box;
    visibility: visible;
    opacity: 1;
    transition: opacity 0.75s ease, right 0.75s ease, visibility 0.85s ease;
    -webkit-overflow-scrolling: touch;
    background: #fff;
    overflow-y: auto;
    z-index: 1111;
    /*_____BEGIN OF HEADER_____*/
    .header {
        padding: 3em 1em 1em 0;

        .logotype {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 0.2em;
            color: #555;
            font-size: 2em;
            font-weight: 700;
            transition: color 0.2s ease;

            &:hover {
                color: hsl(280, 100%, 70%);
            }
        }

        .title {
            margin-bottom: 1.5em;
            color: #aaa;
            font-size: 1em;
            line-height: 18px;
            text-align: right;
        }
    }
    // social bar
    .sidebar-social {
        display: flex;
        justify-content: flex-end;
        margin-top: 1em;
        padding-right: 1em;
    }

    .icon {
        margin-left: 15px;
        color: #555;
        font-size: 1.2em;
        transition: color 0.2s ease;

        &-facebook:hover {
            color: rgb(59,89,152);
        }

        &-instagram:hover {
            color: hsl(280, 100%, 70%);
        }

        &-vk:hover {
            color: #45668e;
        }
    }
    // end of social bar
}
// HEADER MENU
.header-menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 1em;
    letter-spacing: 2px;

    li {
        margin-bottom: 8px;
    }
}

.header-menu-item {
    display: block;
    color: #555;
    transition: padding-right 0.2s ease;
}

.header-menu-item:hover {
    color: hsl(280, 100%, 70%);
    padding-right: 10px;
}
// _____DOTS_____

.owl-dots {
    display: inline-flex;
    flex-wrap: wrap;

    .owl-dot {
        display: inline-block;
        width: 50%;
        position: relative;
        zoom: 1;
        cursor: pointer;
        overflow: hidden;

        img {
            display: block;
            width: 100%;
            // height: 100%;
        }

        &::before {
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            transition: opacity 1s ease;
        }

        &.active:before {
            box-shadow: inset 0 0 0 2px hsl(280, 100%, 70%), inset 0 0 0 3px rgba(0, 0, 0, 0.15);
            opacity: 1;
            transition: opacity 2s ease;
        }
    }
}

.author {
    margin-top: 2em;
    padding-right: 0.5em;
    display: block;
    font-size: 0.9em;
    color: #555;
    line-height: 1.5em;
    text-align: right;

    a {
        color: inherit;
    };
}
// window.onload animation

.is-loading-1 {
    right: -3em;
    opacity: 0;
}

.is-loading-2 {
    opacity: 0;
}
/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
    .slides-wrapper {
        width: calc(100% - 19em);
    }

    .item {
        width: calc(100vw - 265px);
    }

    .sidebar {
        width: 19em;
    }
}
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {}
/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 640px) {
    .sidebar {
        width: 100%;
    }

    .slides-wrapper {
        display: none;
    }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    .owl-dots {
        .owl-dot {
            width: 100%;
        }
    }
}
